
// Fonts
@import url(https://fonts.googleapis.com/icon?family=Material+Icons);

// Variables
@import "variables";

// Utilities
@import "utilities";


//Vue Material fixes
.md-list-item{
  .md-list-item-container{
    display:flex;
    &:hover{
      color:initial;
    }
  }
}

.md-sidenav{
  .md-toolbar-container{
    padding-top:15px;
    flex-direction:column;
  }
}

.md-input-container{
  width:100%;
}


//Bands App Styles
.spinner-container {
  width: 100vw;
  height: calc(100vh - 64px);
  position: absolute;
  background: rgba(255,255,255,0.7);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 2;
}

.content-container{
  margin:25px 75px 10px 75px;
}

.md-table-card{
  .md-toolbar{
    .md-input-container{
      flex:.05 15%;
    }
  }
}